<template>
	<div class="tabCantrol">
		<div v-for="(item,index) in title" :key="index" class="tabCantrolItem" :class="{active: index == currentIndex}" @click="itemClick(index)">
			<span>{{item}}</span>
		</div>
	</div>
</template>
<script>
	export default{
		name:"tabCantrol",
		props:{
			title:{
				type:Array,
				default(){
					return []
				}
			}
		},
		data(){
			return{
				currentIndex:0
			}
		},
		methods:{
			itemClick(index){
				this.currentIndex = index;
				this.$emit('tabclick',index);
			}
		}
	}
</script>
<style scoped>
	.tabCantrol{
		display: flex;
		text-align: center;
		font-size: 15px;
		background-color: #fff;
	}
    .tabCantrolItem{
       flex: 1;
       height: 40px;
       line-height: 40px;
    }
    .tabCantrolItem span{
       padding: 5px;
    }
    .active{
       color: #ff5777;
    }
    .active span{
       border-bottom: 3px solid #ff5777;
    }
</style>