<template>
    <div id="home">
        <navbar class="homeNav">
           <div slot="content">购物街</div>
        </navbar>
        <tabcantrol :title="['流行','新款','精选']" @tabclick="tabclick" class="firsttab" v-show="istab" ref="ftab" />
        <scroll class="content" ref="bscroll" @scroll="contentScroll" :probe="3" :pullUpLoad="true" @pullingUp="loading">
          <swiper>
              <swiperitem v-for="(item,index) in banners.list" :key="index" >
                  <a :href="item.link">
                     <img :src="item.image" alt="" @load="imgload" />
                  </a>
              </swiperitem>
          </swiper>
          <homenav :recommed="recommends" />
          <a href="https://act.mogujie.com/zzlx67">
              <img src="@/assets/img/home/popular_bg.jpg" alt="" class="feature">
          </a>
          <tabcantrol :title="['流行','新款','精选']" @tabclick="tabclick" ref="tab" v-show="!istab" />
          <goodlist :good="goods[tabcantro].list" />
        </scroll>
        <top @click.native="backTop" v-show="istop" />
    </div>
</template>
<script>
    import navbar from "@/components/navbar/navbar"
    import {request} from "@/network/request"
    import {swiper, swiperitem} from "./shuffling"
    import homenav from './homeNav'
    import tabcantrol from '@/components/tabCantrol/tabcantrol'
    import goodlist from './goods/goodList'
    import scroll from '@/components/bscroll/scroll'
    import top from '@/components/top/top'
 export default{
    name:'home',
    data(){
        return{
            banners:[],
            recommends:[],
            goods:{
              pop:{page:1,list:[]},
              new:{page:0,list:[]},
              sell:{page:0,list:[]}
            },
            tabcantro:'pop',
            istop:false,
            isload:false,
            tabTop:0,
            istab:false,
            saveY:0
        }
    },
    created(){
       
       this.shufflingR();
       //请求商品数据
       this.homeNavR('pop');//流行
       this.homeNavR('new');//新款
       this.homeNavR('sell');//精选
       
    },
    mounted(){
       //解决better-scroll在图片未加载完而滑动不流畅的问题
       //$on来拿到图片挂载组件的事件,并执行scroll里让其刷新
       const refresh = this.debounce(this.$refs.bscroll.refresh,200);
       this.$bus.$on('itemImageLoad',()=>{
          refresh();
       })
    },
    activated(){
      //离开该页面会到其原来位置
      this.$refs.bscroll.scrollTo(0,this.saveY,0);
    },
    deactivated(){
      //记录其离开时候的位置
      this.saveY = this.$refs.bscroll.scroll.y;
    },
    methods:{
      imgload(){
        if(!this.isload){
          //$el获取挂载组件
          this.tabTop = this.$refs.tab.$el.offsetTop;
          this.isload = true;
        }
      },
      debounce(func,delay){
         //给scroll刷新做个防抖
         let timer = null
         return function(...args){
            if(timer) clearTimeout(timer)
            timer = setTimeout(()=>{
              func.apply(this,args)
            },delay)
         }
      },
      loading(){
        //上拉加载触发，执行网络请求加载更多数据
        this.homeNavR(this.tabcantro);
        //让其能继续上拉加载
        this.$refs.bscroll.finishPullUp();
      },
      contentScroll(position){
        //判断，显示top
         this.istop = position.y < -1000;
         this.istab = (-position.y) >= (this.tabTop-40);
      },
      backTop(){
        //执行scroll力的方法
         this.$refs.bscroll.scrollTo(0,0,500);
      },
      tabclick(index){
        //监听homeNav传过来的数据，决定显示哪个
         switch(index){
          case 0:
                this.tabcantro = 'pop';
                break
          case 1:
                this.tabcantro = 'new';
                break
          case 2:
                this.tabcantro = 'sell'
                break
         }
         //解决两个tabcantrol点击后显示错误的问题
         this.$refs.ftab.currentIndex = index;
         this.$refs.tab.currentIndex = index;
      },
      /*-----网络请求-----*/
      shufflingR(){
        request({
         url:"/home/multidata"
       }).then(res =>{
        //轮播图数据
          this.banners = res.data.data.banner;
        //homeNav数据
          this.recommends = res.data.data.recommend.list;
       }).catch(err =>{
          console.log("错误："+err)
       })
      },
      homeNavR(type){
        //让数据分成几页来请求
        const pages = this.goods[type].page +1;
        request({
        url:"/home/data",
        params:{
          type:type,
          page:pages
        }
       }).then(res =>{
        //将请求的数据放进对应的goods.list
        this.goods[type].list.push(...res.data.data.list);
        //请求一次让page加1
        this.goods[type].page +=1;
       }).catch(err =>{
        console.log("错误::"+err)
       })
      }
    },
    components:{
      navbar,
      swiper,
      swiperitem,
      homenav,
      tabcantrol,
      goodlist,
      scroll,
      top
    }
 }
</script>
<style scoped>
    #home{
    /*    padding-top: 44px;*/
        height: 100vh;
    }
    .homeNav{
        background-color: #ff8198;
        color: #fff;
    }
    .feature{
        width: calc(100% + 2px);
    }
    .content{
      height: calc(100% - 93px);
      overflow: hidden;
    }
    .fixed{
      position: absolute;
      top: 44px;
      left: 0;
      right: 0;
    }
    .firsttab{
      position: relative;
      z-index: 9;
    }
</style>